.navbar {
  width: 100%;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0% 3%;
  backdrop-filter: blur(40.7742px);
  background: rgba(97, 97, 97, 0.06);

  &__navigation {
    display: flex;
    justify-content: space-between;
    align-items: center;
    // background-color: aqua;

    &__dark-theme {
      color: white;

      a {
        color: white;
      }
    }

    &__light-theme {
      color: rgba(11, 13, 23, 1);
      a {
        color: rgba(11, 13, 23, 1);
      }
    }

    &--not-login {
      width: 50%;

      @media screen and (min-width: 361px) {
        width: 30%;
      }

      @media screen and (min-width: 736px) {
        width: 10%;
      }
    }

    &--login {
      // width: 25%;

      // @media screen and (min-width: 361px) {
      //   width: 15%;
      // }

      @media screen and (min-width: 768px) {
        width: 70%;
      }
    }

    &--links {
      width: 100%;
      display: flex;
      justify-content: space-around;

      @media screen and (max-width: 768px) {
        flex-direction: column;
        position: absolute;
        width: 100vw;
        height: 50vh;
        align-items: center;
        justify-content: center;
        right: 0;

        &__dark-theme {
          background-color: rgba(12, 16, 24);

          @media screen and (max-width: 768px) {
            box-shadow: 0px 4px 5px rgb(40, 39, 39);
          }
        }

        &__light-theme {
          background-color: rgba(225, 222, 222);

          @media screen and (max-width: 768px) {
            box-shadow: 0px 4px 5px rgb(154, 154, 154);
          }
        }

        a {
          margin: 5% 0%;
        }
      }
    }

    &--link {
      text-transform: uppercase;

      &__dark-theme.active {
        border-bottom: 1px solid white;
      }
    }

    &--link {
      &__light-theme.active {
        border-bottom: 1px solid rgba(11, 13, 23, 1);
      }
    }

    &__dark-theme {
      a {
        color: white;
      }
    }

    &__light-theme {
      a {
        color: rgba(11, 13, 23, 1);
      }
    }
  }
}

